#root {
  height: 100vh;
  width: 100vw;
}

.box {
  padding: 2em;
  max-height: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 20px;
}

.gif {
  max-height: 250px;
  max-width: 150px;
  border-radius: 20px;
}

.button {
  border: rgba(255, 255, 255, 0.8) solid 1px;
  background-color: rgba(255, 255, 255, 0.5);
}

.box-light {
  border: #FFFFFFC0 solid 2px;
  color: #FFFFFFC0;
}

.box-dark {
  border: #000000C0 solid 2px;
  color: #000000C0;
}

.background{
  height: 100%;
  width: 100%;
}

